import { _decorator, Component, Node, Prefab, instantiate, EventMouse, Touch, Vec2, Vec3, Button, Collider2D, PhysicsSystem2D, Contact2DType } from 'cc';
import { Card } from './Card';
import { ActionTypes, CARDS_ARRAY } from './GameConstant';
import { GameManager } from './GameManager';
import ReaveldCard from './ReaveldCard';
const { ccclass, property } = _decorator;

@ccclass('Pile')
export class Pile extends Component {
    @property(Prefab)
    cardPrefeb: Prefab;

    arrayOfCard: any[] = [];
    h2: number;

    start() {
        PhysicsSystem2D.instance.enable = true;

        let coll = this.node.getComponent(Collider2D);
        coll.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
    }
    onBeginContact(self: Collider2D, other: Collider2D) {

        if (self.tag == 1 || ActionTypes.MOVE_CARD) {
            console.log("stack1");
        }
        if (self.tag == 2) {
            console.log("stack2");
        }
        if (self.tag == 3) {
            console.log("stack3");
        }
        if (self.tag == 4) {
            console.log("stack4");
        }
        if (self.tag == 5) {
            console.log("stack5");
        }
        if (self.tag == 6) {
            console.log("stack6");
        }
        if (self.tag == 7) {
            console.log("stack7");
        }

        // let selfcollider = event.selfCollider;
        // let otherCollider = event.otherCollider;
        // console.log("self", selfcollider);
        // console.log("other", otherCollider);
    }
    async stacklogic(stack: Node, num) {

        for (let i = 0; i < 7; i++) {
            let card1 = instantiate(this.cardPrefeb);
            card1.getComponent(ReaveldCard).init(this.arrayOfCard[0]);
            this.arrayOfCard.splice(0, 1);
            await this.delay(200, stack.addChild(card1));
            this.h2 = this.arrayOfCard.push(card1);
        }
        let lastCard: ReaveldCard = stack.children[stack.children.length - 1].getComponent(ReaveldCard)
        lastCard.faceDown.active = false;
        lastCard.faceUp.active = true;
        // console.log("last", this.lastindex);
        //console.log("array", this.h2);
    }
    delay(milliseconds: number, count): Promise<number> {
        return new Promise<number>(resolve => {
            setTimeout(() => {
                resolve(count);
            }, milliseconds);
        });
    }
}